@use '../sass/variables' as *;

@mixin mobile {
  @media only screen and (max-width: $mobile) {
    @content;
  }
}

@mixin large {
  @media only screen and (max-width: $large) {
    @content;
  }
}

@mixin desktop {
  @media only screen and (max-width: $desktop) {
    @content;
  }
}